import React, { createRef } from 'react';
import { PlusOutlined } from "@ant-design/icons";
import i18n from '../../i18n';
import { getRequest, postRequest, putRequest } from '../../helpers/superagentAPI'

import { Modal, Button, Form, Input, Row, Col, Divider, Image } from 'antd'
import { Colorpicker, ColorPickerValue } from 'antd-colorpicker'
import toBase64 from "../../helpers/toBase64";

export default class DBModel extends React.Component {

    constructor(props) {
        super(props)
        this.initialState = () => ({
            id: props.id,
            modelName: props.modelName,
            modelTitle: props.title,
            reloadGrid: props.reloadGrid,
            buttonIcon: props.buttonIcon,
            model: null,
            isModalVisible: false,
            imagePreview: '',
            iconPreview: '',
            selectImageFile: null,
            selectIconFile: null
        });
        this.state = this.initialState();
        this.loadModel = async () => {
            if (this.state.id) {
                let data = await getRequest(`${this.state.modelName}/${this.state.id}`)
                this.setState({
                    model: data,
                    imagePreview: `${process.env.REACT_APP_API_URL}${data.image}`,
                    iconPreview: `${process.env.REACT_APP_API_URL}${data.icon}`,

                }, () => {
                    if (this.modelFormRef.current) {
                        this.modelFormRef.current.setFieldsValue({
                            nameEn: data.nameEn,
                            nameAr: data.nameAr,
                            descriptionEn: data.descriptionEn,
                            descriptionAr: data.descriptionAr,
                            pageHeaderEn: data.pageHeaderEn,
                            pageHeaderAr: data.pageHeaderAr,
                            color1: data.color1,
                            color2: data.color2,
                            color3: data.color3,
                            colorIcon: data.colorIcon,
                        })

                    }
                })
            }
        }

        this.imageFileChanged = () => {
            let files = document.getElementById('imageInput').files
            this.setState({
                selectImageFile: files.length != 0 ? files[0] : null
            }, async () => {
                const { selectImageFile } = this.state
                if (selectImageFile == null) {
                    this.setState({
                        imagePreview: '',
                    })
                } else {
                    let imageBase64 = await toBase64(selectImageFile)
                    this.setState({
                        imagePreview: imageBase64,
                    })
                }
            })
        }
        this.iconFileChanged = () => {
            let files = document.getElementById('iconInput').files
            this.setState({
                selectIconFile: files.length != 0 ? files[0] : null
            }, async () => {
                const { selectIconFile } = this.state
                if (selectIconFile == null) {
                    this.setState({
                        iconPreview: ''
                    })
                } else {
                    let imageBase64 = await toBase64(selectIconFile)
                    this.setState({
                        iconPreview: imageBase64
                    })
                }
            })
        }



        this.onFormFinished = async (values) => {
            const { id, modelName, reloadGrid } = this.state
            const { nameEn,
                nameAr,
                descriptionEn,
                descriptionAr,
                pageHeaderEn,
                pageHeaderAr,
                color1,
                color2,
                color3,
                colorIcon } = values
            let formData = new FormData()
            formData.append('nameEn', nameEn)
            formData.append('nameAr', nameAr)
            formData.append('descriptionEn', descriptionEn)
            formData.append('descriptionAr', descriptionAr)
            formData.append('pageHeaderEn', pageHeaderEn)
            formData.append('pageHeaderAr', pageHeaderAr)
            formData.append('color1', color1.hex)
            formData.append('color2', color2.hex)
            formData.append('color3', color3.hex)
            formData.append('colorIcon', colorIcon.hex)
            if (this.state.selectImageFile) formData.append('image', this.state.selectImageFile)
            if (this.state.selectIconFile) formData.append('icon', this.state.selectIconFile)
            if (id) formData.append('id', id)
            await postRequest(modelName, formData)
            if (reloadGrid) reloadGrid()
            this.closeModal()
        }

        this.showModal = () => {
            this.setState({
                isModalVisible: true,
                imagePreview: null,
                iconPreview: null
            }, async () => {
                if (this.state.id) await this.loadModel()
            });
        };

        this.closeModal = () => {
            this.setState({
                isModalVisible: false
            });
        };

        this.modelFormRef = createRef()
    }

    render() {
        const { modelTitle, imagePreview, iconPreview, buttonIcon, id } = this.state
        return (
            <div style={{ width: '100%' }}>
                <Button
                    type="primary"
                    onClick={this.showModal}
                    icon={buttonIcon}
                    shape="round"
                    className="btn_hover darkBlueBg"
                    style={{ width: '100%' }}
                >
                </Button>
                {this.state.isModalVisible && (<Modal title={modelTitle}
                    visible={this.state.isModalVisible}
                    width={'85%'}
                    onCancel={this.closeModal}
                    okButtonProps={{ form: 'modelForm', htmlType: 'submit', className: 'darkBlueBg' }}
                    cancelButtonProps={{ style: { display: 'none' } }}
                >

                    <Form id={'modelForm'} ref={this.modelFormRef} onFinish={this.onFormFinished} style={{ marginTop: '5%' }}>
                        <Row gutter={10}>
                            <Row>
                                <Col span={12}>
                                    {imagePreview != '' && (<><Image src={imagePreview} /><Divider /></>)}
                                    <Form.Item
                                        name={'image'}
                                        label={i18n.t('Image')}
                                        rules={[
                                            {
                                                required: !id,
                                                message: i18n.t('Input is required'),
                                            },
                                        ]}>
                                        <Input type={'file'} accept={'image/*'} id="imageInput"
                                            ref={this.websiteImageFileRef} onChange={this.imageFileChanged} />
                                    </Form.Item>
                                </Col>
                                <Col span={12}>
                                    {iconPreview != '' && (<><Image src={iconPreview} /><Divider /></>)}
                                    <Form.Item
                                        name={'icon'}
                                        label={i18n.t('Icon')}
                                        rules={[
                                            {
                                                required: !id,
                                                message: i18n.t('Input is required'),
                                            },
                                        ]}>
                                        <Input type={'file'} accept={'image/*'} id="iconInput"
                                            ref={this.websiteImageFileRef} onChange={this.iconFileChanged} />
                                    </Form.Item>
                                </Col>
                            </Row>
                            <Col span={24}>
                                <Form.Item
                                    name={'nameEn'}
                                    label={i18n.t('Name English')}
                                    rules={[
                                        {
                                            required: true,
                                            message: i18n.t('input is required'),
                                        },
                                    ]}>
                                    <Input />
                                </Form.Item>
                            </Col>
                            <Col span={24}>
                                <Form.Item
                                    name={'descriptionEn'}
                                    label={i18n.t('Description English')}
                                    rules={[
                                        {
                                            required: true,
                                            message: i18n.t('input is required'),
                                        },
                                    ]}>
                                    <Input.TextArea />
                                </Form.Item>
                            </Col>
                            <Col span={24}>
                                <Form.Item
                                    name={'pageHeaderEn'}
                                    label={i18n.t('Page English')}
                                    rules={[
                                        {
                                            required: true,
                                            message: i18n.t('input is required'),
                                        },
                                    ]}>
                                    <Input.TextArea />
                                </Form.Item>
                            </Col>
                            <Col span={24}>
                                <Form.Item
                                    name={'nameAr'}
                                    label={i18n.t('Name Arabic')}
                                    rules={[
                                        {
                                            required: true,
                                            message: i18n.t('input is required'),
                                        },
                                    ]}>
                                    <Input />
                                </Form.Item>
                            </Col>
                            <Col span={24}>
                                <Form.Item
                                    name={'descriptionAr'}
                                    label={i18n.t('Description Arabic')}
                                    rules={[
                                        {
                                            required: true,
                                            message: i18n.t('input is required'),
                                        },
                                    ]}>
                                    <Input.TextArea />
                                </Form.Item>
                            </Col>
                            <Col span={24}>
                                <Form.Item
                                    name={'pageHeaderAr'}
                                    label={i18n.t('Page Arabic')}
                                    rules={[
                                        {
                                            required: true,
                                            message: i18n.t('input is required'),
                                        },
                                    ]}>
                                    <Input.TextArea />
                                </Form.Item>
                            </Col>
                            <Col span={6}>
                                <Form.Item
                                    name={'color1'}
                                    label={i18n.t('gradient Color 1')}
                                    rules={[
                                        {
                                            required: true,
                                            message: i18n.t('input is required'),
                                        },
                                    ]}>
                                    <Colorpicker />
                                </Form.Item>
                            </Col>

                            <Col span={6}>
                                <Form.Item
                                    name={'color2'}
                                    label={i18n.t('gradient Color 2')}
                                    rules={[
                                        {
                                            required: true,
                                            message: i18n.t('input is required'),
                                        },
                                    ]}>
                                    <Colorpicker />
                                </Form.Item>
                            </Col>

                            <Col span={6}>
                                <Form.Item
                                    name={'color3'}
                                    label={i18n.t('gradient Color 3')}
                                    rules={[
                                        {
                                            required: true,
                                            message: i18n.t('input is required'),
                                        },
                                    ]}>
                                    <Colorpicker />
                                </Form.Item>
                            </Col>

                            <Col span={6}>
                                <Form.Item
                                    name={'colorIcon'}
                                    label={i18n.t('Icon Background Color 3')}
                                    rules={[
                                        {
                                            required: true,
                                            message: i18n.t('input is required'),
                                        },
                                    ]}>
                                    <Colorpicker />
                                </Form.Item>
                            </Col>

                        </Row>
                    </Form>
                </Modal>
                )}
            </div>
        )
    }
}
